"use client";

import { useEffect, useRef, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { SalesService } from "@/services/sales.service";
import EditSaleModal from "@/components/Sales/EditSaleModal";
import toast from "react-hot-toast";
import { ArrowLeft, Printer } from "lucide-react";
import { useReactToPrint } from "react-to-print";

/* =========================
   Types
========================= */
interface SaleItem {
  productId: string;
  productName: string;
  quantity: number;
  unitPrice: number;
  lineTotal: number;
}

interface SaleDetail {
  saleId: string;
  invoiceNumber: string;
  customerName: string;
  branchName: string;
  subTotal: number;
  discount: number;
  totalAmount: number;
  paidAmount: number;
  balance: number;
  paymentStatus: "OPEN" | "PARTIAL" | "CLOSED" | "CANCELLED";
  remark?: string;
  saleDate: string;
  createdBy :string;
  items: SaleItem[];
}

/* =========================
   Cancel Modal
========================= */
function CancelModal({
  isOpen,
  onClose,
  onConfirm,
  isLoading,
}: {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: (remark: string) => void;
  isLoading: boolean;
}) {
  const [remark, setRemark] = useState("Customer requested cancellation");

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center print:hidden">
      <div className="bg-white rounded-xl p-6 max-w-md w-full">
        <h3 className="text-lg font-semibold mb-2">Cancel Sale</h3>
        <textarea
          className="w-full border rounded p-2 mb-4"
          value={remark}
          onChange={(e) => setRemark(e.target.value)}
        />
        <div className="flex justify-end gap-3">
          <button onClick={onClose} className="px-4 py-2 border rounded">
            Close
          </button>
          <button
            disabled={isLoading}
            onClick={() => onConfirm(remark)}
            className="px-4 py-2 bg-red-600 text-white rounded"
          >
            {isLoading ? "Cancelling..." : "Confirm"}
          </button>
        </div>
      </div>
    </div>
  );
}

/* =========================
   Page
========================= */
export default function SaleDetailPage() {
  const { saleId } = useParams<{ saleId: string }>();
  const router = useRouter();

  const [sale, setSale] = useState<SaleDetail | null>(null);
  const [loading, setLoading] = useState(false);

  const [showCancel, setShowCancel] = useState(false);
  const [canceling, setCanceling] = useState(false);

  const [showEdit, setShowEdit] = useState(false);
  const [editing, setEditing] = useState(false);

  const printRef = useRef<HTMLDivElement>(null);

  /* =========================
     PRINT (react-to-print v3)
  ========================= */
  const handlePrint = useReactToPrint({
    documentTitle: `Invoice-${sale?.invoiceNumber}`,
  });

  /* =========================
     LOAD DETAIL
  ========================= */
  async function loadSale() {
    try {
      setLoading(true);
      const res = await SalesService.getSaleDetail(saleId);
      setSale(res.data.data);
    } catch {
      toast.error("Failed to load sale");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    if (saleId) loadSale();
  }, [saleId]);

  /* =========================
     CANCEL
  ========================= */
  async function cancelSale(remark: string) {
    if (!sale) return;
    try {
      setCanceling(true);
      await SalesService.cancelSale({ saleId: sale.saleId, remark });
      toast.success("Sale cancelled");
      setShowCancel(false);
      loadSale();
    } catch {
      toast.error("Cancel failed");
    } finally {
      setCanceling(false);
    }
  }

  /* =========================
     EDIT
  ========================= */
  async function editSale(payload: any) {
    try {
      setEditing(true);
      // await SalesService.editSale(payload);
      toast.success("Sale updated");
      setShowEdit(false);
      loadSale();
    } catch {
      toast.error("Edit failed");
    } finally {
      setEditing(false);
    }
  }

  if (loading) return <p className="p-6">Loading...</p>;
  if (!sale) return <p className="p-6">No sale found</p>;

  const canCancel =
    sale.paymentStatus === "OPEN" || sale.paymentStatus === "PARTIAL";

  return (
    <>
      <CancelModal
        isOpen={showCancel}
        onClose={() => setShowCancel(false)}
        onConfirm={cancelSale}
        isLoading={canceling}
      />

      <EditSaleModal
        open={showEdit}
        saleId={sale.saleId}
        discount={sale.discount}
        remark={sale.remark}
        loading={editing}
        items={sale.items}
        onClose={() => setShowEdit(false)}
        onSubmit={editSale}
      />

      <div className="max-w-6xl mx-auto p-6">
        {/* HEADER */}
        <div className="flex justify-between mb-4 print:hidden">
          <button
            onClick={() => router.back()}
            className="flex items-center gap-2 text-gray-600"
          >
            <ArrowLeft size={16} /> Back
          </button>
        </div>

        {/* PRINT AREA */}
        <div ref={printRef}>
          <div className="text-center mb-6">
            <h1 className="text-2xl font-bold">Sales Details</h1>
            <p>Invoice #{sale.invoiceNumber}</p>
            <p>{new Date(sale.saleDate).toLocaleString()}</p>
          </div>

          <div className="grid grid-cols-2 gap-4 border p-4 mb-6">
            <div>
          <p><strong>Created User:</strong> {sale.createdBy}</p>

              <p><strong>Customer:</strong> {sale.customerName}</p>
              <p><strong>Branch:</strong> {sale.branchName}</p>
              <p><strong>Status:</strong> {sale.paymentStatus}</p>
            </div>
            <div>
              <p><strong>Subtotal:</strong> {sale.subTotal}</p>
              <p><strong>Discount:</strong> {sale.discount}</p>
              <p><strong>Total:</strong> {sale.totalAmount}</p>
              <p><strong>Paid:</strong> {sale.paidAmount}</p>
              <p><strong>Balance:</strong> {sale.balance}</p>
            </div>
          </div>

          <table className="w-full border">
            <thead className="bg-gray-100">
              <tr>
                <th className="p-2 text-left">Product</th>
                <th className="p-2 text-right">Qty</th>
                <th className="p-2 text-right">Price</th>
                <th className="p-2 text-right">Total</th>
              </tr>
            </thead>
            <tbody>
              {sale.items.map((i, idx) => (
                <tr key={idx} className="border-t">
                  <td className="p-2">{i.productName}</td>
                  <td className="p-2 text-right">{i.quantity}</td>
                  <td className="p-2 text-right">{i.unitPrice}</td>
                  <td className="p-2 text-right">{i.lineTotal}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* ACTIONS */}
        <div className="flex gap-3 mt-4 print:hidden">
          <button
            onClick={() => setShowEdit(true)}
            className="px-4 py-2 bg-blue-600 text-white rounded"
          >
            Edit Sale
          </button>

          {canCancel && (
            <button
              onClick={() => setShowCancel(true)}
              className="px-4 py-2 bg-red-600 text-white rounded"
            >
              Cancel Sale
            </button>
          )}
        </div>
      </div>
    </>
  );
}
