"use client";

import { useState } from "react";

interface EditItem {
  productId: string;
  productName: string;
  quantity: number;
  unitPrice: number;
}

interface Props {
  open: boolean;
  saleId: string;
  discount: number;
  remark?: string;
  items: EditItem[];
  loading: boolean;
  onClose: () => void;
  onSubmit: (payload: any) => void;
}

export default function EditSaleModal({
  open,
  saleId,
  discount,
  remark,
  items,
  loading,
  onClose,
  onSubmit,
}: Props) {
  const [localDiscount, setLocalDiscount] = useState(discount);
  const [localRemark, setLocalRemark] = useState(remark ?? "");
  const [localItems, setLocalItems] = useState(items);

  if (!open) return null;

  function updateItem(index: number, key: string, value: number) {
    const updated = [...localItems];
    updated[index] = { ...updated[index], [key]: value };
    setLocalItems(updated);
  }

  function submit() {
    const payload = {
      saleId,
      discount: localDiscount,
      remark: localRemark,
      items: localItems.map((i) => ({
        productId: i.productId,
        quantity: i.quantity,
        unitPrice: i.unitPrice,
        lineTotal: i.quantity * i.unitPrice,
      })),
    };

    onSubmit(payload);
  }

  return (
    <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center">
      <div className="bg-white rounded-xl w-full max-w-3xl p-6">
        <h3 className="text-lg font-semibold mb-4">Edit Sale</h3>

        {/* ITEMS */}
        <table className="w-full mb-4">
          <thead>
            <tr className="border-b">
              <th className="text-left py-2">Product</th>
              <th className="text-right">Qty</th>
              <th className="text-right">Unit Price</th>
              <th className="text-right">Total</th>
            </tr>
          </thead>
          <tbody>
            {localItems.map((item, i) => (
              <tr key={i} className="border-b">
                <td className="py-2">{item.productName}</td>
                <td className="text-right">
                  <input
                    type="number"
                    className="w-20 border rounded px-2 py-1"
                    value={item.quantity}
                    onChange={(e) =>
                      updateItem(i, "quantity", Number(e.target.value))
                    }
                  />
                </td>
                <td className="text-right">
                  <input
                    type="number"
                    className="w-24 border rounded px-2 py-1"
                    value={item.unitPrice}
                    onChange={(e) =>
                      updateItem(i, "unitPrice", Number(e.target.value))
                    }
                  />
                </td>
                <td className="text-right font-medium">
                  {item.quantity * item.unitPrice}
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* DISCOUNT */}
        <div className="mb-3">
          <label className="text-sm text-gray-600">Discount</label>
          <input
            type="number"
            value={localDiscount}
            onChange={(e) => setLocalDiscount(Number(e.target.value))}
            className="w-full border rounded px-3 py-2"
          />
        </div>

        {/* REMARK */}
        <div className="mb-4">
          <label className="text-sm text-gray-600">Remark</label>
          <textarea
            value={localRemark}
            onChange={(e) => setLocalRemark(e.target.value)}
            className="w-full border rounded px-3 py-2"
          />
        </div>

        {/* ACTIONS */}
        <div className="flex justify-end gap-3">
          <button onClick={onClose} className="px-4 py-2 border rounded">
            Cancel
          </button>
          <button
            disabled={loading}
            onClick={submit}
            className="px-4 py-2 bg-blue-600 text-white rounded"
          >
            {loading ? "Saving..." : "Save Changes"}
          </button>
        </div>
      </div>
    </div>
  );
}
